.radial-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(193, 206, 217, 0.8549019608);
  border: 0 solid transparent;
  border-radius: 50%;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.05);
  width: 600px;
  height: 600px;
  opacity: 1;
  position: relative;
  transition: opacity 0.1s ease, background 0.1s ease;
}
.radial-menu.is-hidden {
  animation-name: scale-down;
  animation-duration: 0.2s;
  animation-delay: 0;
  animation-iteration-count: 1;
  animation-fill-mode: forwards; /* this prevents the animation from restarting! */
  animation-timing-function: cubic-bezier(0.25, 0.25, 0.25, 1.25);
  transform: translate(-50%, -50%) scale(0) !important;
  opacity: 0;
}
.radial-menu.is-active {
  animation-name: scale-up;
  animation-duration: 0.2s;
  animation-delay: 0;
  animation-iteration-count: 1;
  animation-fill-mode: forwards; /* this prevents the animation from restarting! */
  animation-timing-function: cubic-bezier(0.25, 0.25, 0.25, 1.25);
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}
.radial-menu.item-is-hovered {
  background-color: #726472;
  border-color: transparent;
  border-width: 0;
}

@keyframes scale-up {
  0% {
    transform: translate(-50%, -50%) scale(0);
  }
  100% {
    transform: translateX(-50%, -50%) scale(1);
  }
}
@keyframes scale-down {
  0% {
    transform: translate(-50%, -50%) scale(1);
  }
  100% {
    transform: translateX(-50%, -50%) scale(0);
  }
}
.radial-menu__label {
  background-color: rgba(231, 236, 241, 0.8549019608);
  border: 3px solid rgba(206, 216, 225, 0.8549019608);
  border-radius: 50%;
  color: rgba(193, 206, 217, 0.8549019608);
  cursor: default;
  font-size: 16px;
  font-weight: bold;
  height: 100px;
  left: 50%;
  line-height: 94px;
  position: absolute;
  text-align: center;
  text-transform: uppercase;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  z-index: 4;
}

.radial-menu__menu-content {
  background-color: rgba(231, 236, 241, 0.8549019608);
  border: 3px solid rgba(206, 216, 225, 0.8549019608);
  border-radius: 50%;
  height: 80px;
  left: 50%;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  z-index: 1;
}

.radial-menu__menu-item.hovered .radial-menu__menu-content {
  background-color: #625662;
  border-color: #625662;
  transform: translate(-50%, -50%);
  z-index: 4;
}

.radial-menu__menu-content-wrapper {
  padding-left: 10px;
  padding-right: 10px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
}

.radial-menu__menu-content-title {
  color: #fff;
  font-size: 1.4em;
  font-weight: bold;
  line-height: 5;
}

.radial-menu__menu-content-description {
  color: rgba(255, 255, 255, 0.5);
  display: none;
  font-size: 0.4em;
  font-weight: normal;
  letter-spacing: 0.1em;
  line-height: 1.3;
  text-transform: uppercase;
}

.radial-menu__menu-icon {
  background-color: transparent;
  height: 25px;
  left: 0;
  position: absolute;
  text-align: center;
  top: 0;
  transform: translateY(-50%) translateX(-50%);
  width: 25px;
  z-index: 3;
}
.radial-menu__menu-icon .oi {
  position: relative;
  opacity: 0.7;
  color: #331d2c;
  font-size: 1rem;
  line-height: 25px;
  transition: font-size 0.3s cubic-bezier(0.25, 0.25, 0.25, 1.5), color 0.1s ease;
}

.radial-menu.item-is-hovered .radial-menu__menu-icon {
  background-color: transparent;
}
.radial-menu.item-is-hovered .radial-menu__menu-icon .oi {
  color: rgba(168, 186, 202, 0.8549019608);
}

.radial-menu.item-is-hovered .radial-menu__menu-item {
  cursor: pointer;
}
.radial-menu.item-is-hovered .radial-menu__menu-item.hovered .radial-menu__menu-icon {
  background-color: transparent;
  height: 50px;
  width: 50px;
}
.radial-menu.item-is-hovered .radial-menu__menu-item.hovered .radial-menu__menu-icon .oi {
  color: #fff;
  font-size: 21px;
  line-height: 50px;
}

.radial-menu__menu-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.radial-menu__menu-item {
  display: block;
  left: 50%;
  position: absolute;
  top: 50%;
  z-index: 1;
}
.radial-menu__menu-item.hovered {
  z-index: 10;
}

.radial-menu__menu-link,
.radial-menu__menu-link-bg {
  height: 43px;
  padding: 5px 20px;
  position: absolute;
  transform: translateY(-50%) perspective(200px) rotateY(-77.5deg) scaleX(1.25);
  transform-origin: 0;
  transform-style: preserve-3d;
  width: 254px;
}

.radial-menu__menu-link {
  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;
  display: block;
  z-index: 3;
  color: transparent;
}

.radial-menu__menu-link-bg {
  background-color: rgba(218, 226, 233, 0.8549019608);
  border-bottom: 1px solid rgba(212, 221, 229, 0.8549019608);
  border-top: 1px solid rgba(212, 221, 229, 0.8549019608);
  font-size: 25px;
  text-decoration: none;
  text-transform: uppercase;
  transition: background 0.05s ease;
  z-index: 1;
}
.radial-menu__menu-link-bg:after {
  background: rgba(193, 206, 217, 0.8549019608);
  content: "";
  display: none;
  height: 106%;
  right: -1px;
  position: absolute;
  top: 0;
  transition: all 0.1s ease;
  transform: translateY(-3%);
  width: 2px;
}

.radial-menu__menu-item.hovered .radial-menu__menu-link-bg {
  background-color: #726472;
  border: 0;
}
.radial-menu__menu-item.hovered .radial-menu__menu-link-bg:after {
  background: #726472;
  height: 106%;
  transform: translateY(-3%);
  width: 0;
}

