*/
@media only screen and (max-width: 1024px) {
  header .nav-btn {
    visibility: visible;
    opacity: 1;
  }
  header nav {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    background-color: var(--mainColor);
    transition: 1s;
    transform: translateY(-100vh);
  }
  header nav .nav-close-btn {
    position: absolute;
    top: 2rem;
    right: 2rem;
  }
  header nav a {
    font-size: 1.5rem;
  }
  header .responsive-nav {
    transform: none;
